@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap');
/**/

body {
  background-color: hsl(235, 16%, 14%)
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
}

.icon:hover {
  cursor: pointer;
  filter: invert(57%) sepia(98%) saturate(2316%) hue-rotate(309deg) brightness(98%) contrast(101%);
}

.icon-container {
  display: flex;
  justify-content: space-evenly;
  width: 14%;
  margin-top: 200px;
}

.background {
  position: absolute;
  z-index: -1;
}

h1 {
  font-size: 1.7em;
  color: hsl(0, 0%, 100%);
  font-family: 'Red Hat Text', sans-serif;
  letter-spacing: .3em;
  margin: 50px 0;
}

@media (max-width: 992px) {
  h1 {
    font-size: 1.3em;
  }
  .icon-container {
    width: 18%;
  }
}

@media (max-width: 768px) {
  h1 {
    font-size: 1.1em;
    letter-spacing: .4em;
  }
  .icon-container {
    width: 24%;
  }
}

@media (max-width: 600px) {
  h1 {
    font-size: 1em;
    letter-spacing: .4em;
  }
  .icon-container {
    width: 28%;
  }
}