@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap');
/**/

.main-container {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.card-container {
    display: flex;
    justify-content: space-evenly;
}

.column-align {
    align-items: center;
    display: flex;
    flex-direction: column;
}

.num-card {
    align-items: center;
    background-color: hsl(236, 21%, 26%);
    border-radius: 4px;
    box-shadow: 0px 9px 2px 0px rgba(0, 0, 0, .2);
    color: hsl(345, 95%, 68%);
    display: flex;
    font-family: 'Red Hat Text', sans-serif;
    font-size: 3em;
    height: 100px;
    justify-content: center;
    padding: .15em .15em;
    width: 105px;
}

.label-text {
    color: hsl(237, 18%, 59%);
    font-family: 'Red Hat Text', sans-serif;
    font-size: 14px;
    letter-spacing: .3em;
    margin-top: 1.3em;
}

@media (max-width: 992px) {
    .num-card {
        height: 85px;
        width: 90px;
        font-size: 2em;
        margin: 0 .3em;
    }
    .label-text {
        font-size: 12px;
    }
}

@media (max-width: 768px) {
    .num-card {
        height: 75px;
        width: 80px;
        font-size: 2em;
        margin: 0 .3em;
    }
}

@media (max-width: 600px) {
    .num-card {
        height: 60px;
        width: 65px;
    }
}